

























































import Vue from "vue";
import Terms from "../components/Terms.vue";
export default Vue.extend({
	name: "Register",
	components: {
		Terms
	},
	data() {
		return {
			email: "",
			password: "",
			name: "",
			profileImage: null as any,

			errorAlert: ""
		};
	},
	methods: {
		enterPress(e: any) {
			if (e.keyCode == 13) this.register();
		},
		encodeBase64ImageFile(image: File): Promise<string> {
			return new Promise<string>((resolve, reject) => {
				if (!image) return resolve("");
				let reader = new FileReader();
				reader.readAsDataURL(image);
				reader.onload = (event: any) => {
					resolve(event.target.result);
				};
				reader.onerror = error => {
					reject(error);
				};
			});
		},
		onChangeFile(e: any) {
			this.profileImage = e.target.files[0];
		},
		register() {
			this.$store.commit("pushLoading", {
				name: "REGISTER",
				message: "회원가입 하는 중"
			});
			this.encodeBase64ImageFile(this.profileImage)
				.then(img => {
					this.$store
						.dispatch("REGISTER", {
							email: this.email,
							password: this.password,
							name: this.name
						})
						.then(token => {
							if (img) {
								this.$store
									.dispatch("SET_PROFILE_IMAGE", {
										img
									})
									.then(() => {
										this.$store.commit(
											"clearLoading",
											"REGISTER"
										);
										this.$router.push("/login");
									})
									.catch(err => {
										this.$store.commit(
											"clearLoading",
											"REGISTER"
										);
										this.errorAlert =
											err.response.data.message;
									});
							} else {
								this.$store.commit("clearLoading", "REGISTER");
								this.$router.push("/login");
							}
						})
						.catch(err => {
							this.$store.commit("clearLoading", "REGISTER");
							this.errorAlert = err.response.data.message;
						});
				})
				.catch(err => {
					this.$store.commit("clearLoading", "REGISTER");
					this.errorAlert = err.response.data.message;
				});
		}
	}
});
